<template>
		<uni-popup ref="popup" background-color="#fff" @change="change" style="height: 300px;background-color: aqua !important;;">
			<view class="popup-content">
				<view style="margin: 5px 7px;font-size: 14.5px;display: flex;justify-content: space-between;align-items: center;">
					<view>
						<text style="color: red;margin-left: 5px;">*</text>
						<text style="color: black;margin-left: 2px;">Test Duration</text>
						<text style="color: #c13a37;margin-left: 10px;">Please Fill In The Testing Duration</text>
					</view>
					<uni-icons type="closeempty" @click="close" size="25" color="#626262" style=""></uni-icons>
				</view>
				
			</view>
			<view style="width: 100%;height: 100%;">
				<uni-grid  :column="2" :show-border="false" :square="false" >
					  <uni-grid-item  style="width: 45%;border-radius: 5px !important;" v-for="(item, index) in durationList" :key="index">
						<view @click="changeGrid(item,index)" :class="index==selectedGridIndex ?'grid-item-click':'grid-item'">{{ item.value }}</view>
					  </uni-grid-item>
				</uni-grid>
				<view style="margin: 4% 6%;">
					<button style="background-color: #193c47;color: #fff;" @click="sure">OK</button>
				</view>
			</view>
			
		</uni-popup>
	
</template>

<script>
	
	export default {
		name: 'testDuration',
		props: {
		},
		data() {
			return {
				list:[],//
				selectedId:'',
				durationList:[
					{index:"1",value:"0.5Hr"},
					{index:"2",value:"1Hr"},
					{index:"3",value:"1.5Hr"},
					{index:"4",value:"2Hr"},
					{index:"5",value:"2.5Hr"},
					{index:"6",value:"3Hr"},
					{index:"7",value:"3.5Hr"},
					{index:"8",value:"4Hr"},
					{index:"9",value:"4.5Hr"},
					{index:"10",value:"5Hr"},
					{index:"11",value:"5.5Hr"},
					{index:"12",value:"6Hr"},
					{index:"13",value:"6.5Hr"},
					{index:"14",value:"7Hr"},
					{index:"15",value:"7.5Hr"},
					{index:"16",value:"8.0Hr"},
				],
				selectedGridIndex: -1,//点击宫格后背景色变红
				tempTestDuration:null,//点击宫格后选择的时长

			}
		},
		methods: {
			changeGrid(data,index) {
				let that = this
				that.selectedGridIndex = index;
				that.tempTestDuration = data.value.replace('Hr', '')
				console.log(that.selectedGridIndex)
				console.log(that.tempTestDuration)
			},
			close(){
				let that = this;
				that.selectedGridIndex = -1;
				that.tempTestDuration = null;
				this.$refs.popup.onTap();
			},
			//列表
			getList(value){				
				let that = this;
			},
			change(e) {
				console.log('当前模式：' + e.type + ',状态：' + e.show);
			},
			cancel(){
				let that = this;
				that.selectedGridIndex = -1;
				that.tempTestDuration = null;
				that.$refs.popup.onTap();		 
			},
			sure(){
				let that = this
				if(that.tempTestDuration){
					that.$refs.popup.onTap();
					that.$emit('handleSubmit',that.tempTestDuration)
				}else{
					uni.showToast({
						title: 'Please Confirm Test Duration',
						icon: 'error'
					});
					return 
				}
				that.selectedGridIndex = -1;
				that.tempTestDuration = null;
			},			
			toggle(type) {
				this.getList('')
				this.type = type
				// open 方法传入参数 等同在 uni-popup 组件上绑定 type属性
				this.$refs.popup.open(type)
				this.$refs.popup.vshow=false
			},
	  }
		
	}
</script>

<style lang="scss" scoped scoped>
	
	page {
		
		min-height: 100%;
		background-color: #FFFFFF;
		
	}
    ::v-deep uni-checkbox{
        display: flex;
    }
    ::v-deep uni-checkbox:not([disabled]) .uni-checkbox-input:hover{
        border-color:#d1d1d1;
    }
    ::v-deep uni-checkbox .uni-checkbox-input.uni-checkbox-input-checked{
            color: #ffffff !important;
            background: #0289fe;
            border-color:#0289fe !important;
    }
    ::v-deep uni-checkbox .uni-checkbox-input{
        border-radius: 50%;
        width: 38upx;
        height: 38upx;
        margin-right: 20upx;
    }
    ::v-deep uni-checkbox-group{
        height: 78%;
    }
    .popup-content {
        height:15% !important; 
        ::v-deep .uni-searchbar{
            padding: 20upx 30upx;
        }
    }
	.pop-wuzi{
		height: calc(60vh);
		overflow-y: auto;
		padding: 0 30upx;
		.wuzi-list{
			display: flex;
			align-items: center;
			padding: 20upx 0;
			border-bottom: 1upx solid #f5f5f5;
			.title-g{
				flex: 1;
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				view{
					font-size: 26upx;
				}
				view:last-of-type{
					font-size: 24upx;
					color: #888888;
				}
			}
		}
		
	}
    ::v-deep .pop-content{
        height: 80%!important;
        border-top-right-radius: 30upx;
        border-top-left-radius:30upx;
        overflow: hidden;
    }
    ::v-deep uni-view{
        border-radius: 10px 10px 0 0  !important;
    }
    ::v-deep .uni-grid{
        display: flex;
        justify-content: center;
    }
	.grid-item {
	  width: 95%;
	  height: 100%;
	  border-radius: 3px !important;
	  background-color: #f0f0f0;
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  margin: 3% 3%;
	  padding: 3% 0;
	}
	.grid-item-click {
	  width: 95%;
	  height: 100%;
	  border-radius: 3px !important;
	  background-color: #e67464;
	  color: #FFFFFF;
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  margin: 3% 3%;
	  padding: 3% 0;
	}
	.active-bg {
	  background-color: red; /* 被点击的宫格背景色设置为红色 */
	}
</style>
