<template>
		<uni-popup ref="popup" background-color="#fff" @change="change" style="height: 300px;background-color: aqua !important;;">
			<view class="popup-content">
				<view style="margin: 5px 7px;font-size: 14.5px;display: flex;justify-content: space-between;align-items: center;">
					<view>
						<text style="color: black;margin-left: 5px;">Please Select the Result</text>
					</view>
				</view>
				
			</view>
			<view style="width: 100%;height: 100%;">
				<view class="pop-wuzi">
					<view class="wuzi-list" v-for="(item,index) in measureList" :key="item.id">
						<view class="title-g" @click="batchEdit(item)">
							<view>{{item.value}}</view>
						</view>
					</view>
				</view>
			</view>
			
		</uni-popup>
	
</template>

<script>
	
	export default {
		name: 'measuredValue',
		props: {
			selectRows: {
			    type: Array,
			    default: function() {
			      return [];
			    }
			  }
		},
		data() {
			return {
				measureList:[
					{id:"1",value:"i.o"},
					{id:"2",value:"not tested"},
					{id:"3",value:"not testable"},
				],
				tempTestDuration:null,//点击宫格后选择的时长

			}
		},
		methods: {
			batchEdit(item) {
				let that = this
				if(!that.selectRows.length){
					uni.showToast({
						title: 'Please select at least one piece of data',
						icon: 'none'
					});
					return
				}
				that.$emit('handleBatchEdit',item)
				that.$refs.popup.onTap(); 
			},
			close(){
				this.$refs.popup.onTap();
			},
			//列表
			getList(value){				
				let that = this;
			},
			change(e) {
				console.log('当前模式：' + e.type + ',状态：' + e.show);
			},
			cancel(){
				let that = this;
				that.$refs.popup.onTap();		 
			},
			sure(){
				let that = this
				that.$emit('handleEdit',{selectMeasuredValue:that.selectMeasuredValue,value:'i.o'})
				that.$refs.popup.onTap(); 
			},			
			toggle(type) {
				this.getList('')
				this.type = type
				// open 方法传入参数 等同在 uni-popup 组件上绑定 type属性
				this.$refs.popup.open(type)
				this.$refs.popup.vshow=false
			},
	  }
		
	}
</script>

<style lang="scss" scoped scoped>
	
	page,
	view {
		/* display: flex; */
	}
	
	page {
		
		min-height: 100%;
		background-color: #FFFFFF;
		
	}
    ::v-deep uni-checkbox{
        display: flex;
    }
    ::v-deep uni-checkbox:not([disabled]) .uni-checkbox-input:hover{
        border-color:#d1d1d1;
    }
    ::v-deep uni-checkbox .uni-checkbox-input.uni-checkbox-input-checked{
            color: #ffffff !important;
            background: #0289fe;
            border-color:#0289fe !important;
    }
    ::v-deep uni-checkbox .uni-checkbox-input{
        border-radius: 50%;
        width: 38upx;
        height: 38upx;
        margin-right: 20upx;
    }
    ::v-deep uni-checkbox-group{
        height: 78%;
    }
    .popup-content {
        height:15% !important; 
        ::v-deep .uni-searchbar{
            padding: 20upx 30upx;
        }
    }
	.pop-wuzi{
		height: calc(30vh);
		overflow-y: auto;
		padding: 0 30upx;
		.wuzi-list{
			display: flex;
			align-items: center;
			padding: 20upx 0;
			border-bottom: 5upx solid #f1f4f5;
			.title-g{
				flex: 1;
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;
				view{
					font-size: 30upx;
				}
				view:last-of-type{
					font-size: 24upx;
					color: #888888;
				}
			}
		}
		
	}
    ::v-deep .pop-content{
        height: 80%!important;
        border-top-right-radius: 30upx;
        border-top-left-radius:30upx;
        overflow: hidden;
    }
    ::v-deep uni-view{
        border-radius: 10px 10px 0 0  !important;
    }
</style>
