<template>
	<view class="riskAssessment" :style="{height:containerHeight,width:containerWidth}" @touchmove.prevent>
		<!-- <view class="status_bar"></view> -->
		<view>
			<uni-nav-bar style="background-color:#ffffff;color:black;font-weight: bold;" shadow status-bar
				left-icon="left" title="Check Items" left-width="20%" right-width="20%" :right-text="rightText"
				@clickRight="batchEdit" @clickLeft="back">
			</uni-nav-bar>
			<measured-value ref="measuredValues" @handleBatchEdit="handleBatchEdit"
				:selectRows="selectRows"></measured-value>
		</view>
		<view class="infor-list">
			<scroll-view ref="scrollController" :scroll-anchoring="true"  :scroll-top="scrollTop" scroll-y='true' id="scrollContainer" 
			 style=" height:75%;position: fixed;background-color: #f1f4f5;">
				<view style="margin:0px 10px 10px 10px;">
					<view v-for="(item,index) in listGroup" :key="item.id">
						<view style="display: flex;justify-content: space-between;;">
							<view v-if="listGroup.length>0"
								style="color: #9c9c9c;font-size: 14px;font-weight: bold;padding: 2%">Item
								ID：{{item.itemId}}</view>
							<view v-if="listGroup.length>0"
								:style="item.measuredValue == 'n.i.o' ? resultStyleRed : resultStyleBlue">
								{{item.measuredValue}}
							</view>
						</view>
						<view v-for="(childrenItem,index) in item.checkItems" :key="childrenItem.id">
							<label class="list">
								<view class="list-part">
									<view class="part-title">
										<checkbox-group class="part-title" @change="checkboxChange(childrenItem)">
											<!--  @click="checkboxChange(childrenItem)" -->
										<checkbox v-if="showCheckbox"
											:value="childrenItem.id" :disabled="childrenItem.testResult=='n.i.o'" class="checkbox"></checkbox>
										<view class="title-t">
											<view style="display: flex;flex: 1 1; align-items: center;">
												{{childrenItem.checkItems}}
											</view>
											<view style="display: flex;">
												<p class="testcase" @click="goDetailDescription(index)">View
													Testcase</p>
												<p v-show="childrenItem.testResult" class="testResult">{{childrenItem.testResult}}</p>	
											</view>
										</view>
										<view class="edit" @click="edit(childrenItem)">
											<p class="red-dot-text" v-show="!childrenItem.testResult"></p>Edit
											<uni-icons type="right" size="15" color="#c9c9c9"
												style="padding-right: 7%;"></uni-icons>
										</view>
										</checkbox-group>
									</view>
								</view>
							</label>
						</view>
					</view>
				</view>
			</scroll-view>
		</view>
		<view
			style="position: fixed;bottom: 10px;width: 100%;display: flex;justify-content: center;align-items: center;">
			<button type="warn" style="width: 90%" @click="submit">Submit</button>
		</view>
		<!-- display: flex;margin-top: 5%;justify-content: center; -->
		<test-duration ref="testDuration" @handleSubmit="handleSubmit"></test-duration>
	</view>
</template>

<script>
	import {
		randomUUID
	} from '@/common/util/util.js'
    import testDuration from '../task/testDuration.vue';
    import { API } from '@/api/api';
	import measuredValue from '../task/measuredValue.vue';
	import bus from '../task/temporaryBus.js'

	export default {
		components: {
			testDuration,
			measuredValue
		},
		data() {
			return {
				scrollTop:0.01,
				oldScrollTop:0.01,
				containerHeight: '0',
				containerWidth: '0',
				showCheckbox: false,
				url: {},
				tqsResultData: [],
				listGroup: [],
				selectRows: [], //批量编辑，勾选的数据
				rightText: "Batch Edit",
				resultStyleRed:"color:#ff0000;font-size: 14px;font-weight: bold;padding: 7px 7%;",
				resultStyleBlue:"color:#2b9c94;font-size: 14px;font-weight: bold;padding: 7px 7%;",
			}
		},
		onLoad(e) {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
				plus.navigator.setStatusBarStyle("dark")
			}
			if (e.data != undefined && e.data != null && e.data != null) {
				this.tqsResultData = JSON.parse(e.data)
				this.getCheckItems(this.tqsResultData)
			} else {
				this.$nextTick(() => {
					if (e.data != undefined && e.data != null && e.data != null) {
						let data = JSON.parse(e.resultData)
						this.getCheckItems(data)
					} else {
						let data = JSON.parse(this.$route.query.resultData)
						this.getCheckItems(data)
					}
				})
			}
			if(e.scrollTop){
				this.oldScrollTop = e.scrollTop
			}
		},
		onShow() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
				plus.navigator.setStatusBarStyle("dark")
			}
			this.getCheckItems(this.tqsResultData)
		},
		created() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
				plus.navigator.setStatusBarStyle("dark")
			}
			this.getScreenHeight()
		},
		mounted() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
				plus.navigator.setStatusBarStyle("dark")
			}
			this.getScreenHeight()
		},
		onReady() {
			const info = uni.getSystemInfoSync();
			if (info.uniPlatform.toLowerCase() === 'app') {
				plus.navigator.setStatusBarStyle("dark")
			}
		},
		methods: {
			// scroll: function(e) {
			// 	this.scrollTop =  e.detail.scrollTop
			// },
			goLastScrollPositive(){
				this.scrollTop = 0.01
				this.$nextTick(function() {
					this.scrollTop = this.oldScrollTop
				});
			},
			getCheckItems(data) {
				let that = this
				if (data && data.id) {
					let params = {
						resultId: data.id,
						isTemp: '1'
					}
                    that.$http.get(API.testCase.qaTestResultSubtable_appList, {
                        params: params
                    }).then(res => {
						if (res.data.success) {
							that.listGroup = res.data.result
							that.goLastScrollPositive()
						}
					})
				}
			},
			goDetailDescription(index) {
				let listGroupDescription = ''
				this.listGroup.forEach(item => {
					item.checkItems.forEach(itemData => {
						listGroupDescription = itemData.inspectionContentEn
					})

				})
				uni.navigateTo({
					url: '../task/detailDescription?listGroupDescription=' + listGroupDescription
				})
			},
			handleBatchEdit(item) {
				let that = this
				//获取勾选的数据
				let selectIds = that.selectRows
				let params = {
					ids: selectIds,
					testResult: item.value
				}
                that.$http.post(API.testCase.qaTestResultSubtable_batchEditTestResult, params).then(res => {
					if (res.data.success) {
						uni.showToast({
							icon: 'none',
							title: res.data.message,
						})
						that.rightText = 'Batch Edit'
						this.getCheckItems(this.tqsResultData)
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
					}
				})
				//item  中有想要把勾选数据修改成的id 或者 measredValue
				//调取后端接口，进行批量修改，修改success后  清空selectRows的值,然后关闭复选框
				that.selectRows = []
				that.showCheckbox = false
			},
			handleSubmit(testDuration) {
				let that = this
				let resultData = that.tqsResultData
				resultData.testDuration = testDuration
                that.$http.post(API.testCase.appResultSubmit, resultData).then(res => {
					if (res.data.success) {
						uni.showToast({
							title: 'Submit Successfully',
						});
						uni.reLaunch({
							url: '/pages/index/index'
						})
					} else {
						if(res.data.message){
							uni.showToast({
								icon:'none',
								title: res.data.message
							});
						}else{
							uni.showToast({
								icon:'error',
								title: 'Submit Failed',
							});
						}
						
						return
					}
				})

			},
			checkboxChange(item) {
				let that = this
				if(item.testResult && item.testResult == 'n.i.o'){
					uni.showToast({
						icon:'none',
						duration:3000,
						title: 'The data of the currently selected check item has a check result of n.i.o and cannot be batch edited!',
					});
					return false
				}
				let id = item.id
				//本次勾选的数据
				let selectTempRows = that.selectRows
				if (selectTempRows != null && selectTempRows.length > 0) {
					//如果此时包含选择的id,说明已经选中过了，再次勾选是为了取消，所以要删除；
					//如果不包含，说明没有勾选过，需要勾选上，所以要添加
					if (selectTempRows.indexOf(id) >= 0) {
						selectTempRows.splice(selectTempRows.indexOf(id), 1);
					} else {
						selectTempRows.push(id);
					}
				} else {
					//如果此时数组是空的，直接插入
					selectTempRows.push(id);
				}
				that.selectRows = selectTempRows;
			},
			batchEdit() {
				let that = this
				if (that.rightText == 'Test Result') {
					that.complete()
				} else {
					that.showCheckboxFunction()
				}
			},
			showCheckboxFunction() {
				let that = this
				that.showCheckbox = true
				that.rightText = "Test Result"
			},
			complete() {
				let that = this
				//需要处理的被选中的数据的key集合
				let handleSelectData = that.selectRows
				/* 
				 * 执行批量修改的业务
				 */
				that.$refs.measuredValues.toggle('bottom')
			},
			submit() {
				let that = this
				that.$refs.testDuration.toggle('bottom')
			},
			back() {
				uni.reLaunch({
					url: '/pages/index/index',
					fail: function(error) {
						console.log('跳转失败', error);
					}
				})
			},
			edit(data) {
				const query = uni.createSelectorQuery().in(this);
				    // 选择 scrollContainer 元素
				    query.select('#scrollContainer').scrollOffset((res) => {
				    // res.scrollTop 是当前的滚动位置
				    console.log('当前滚动位置:', res.scrollTop);
					uni.navigateTo({
						url: '../task/checkItemsEdit?data=' + encodeURIComponent(JSON.stringify(data))+ '&resultData=' + JSON.stringify(
							this.tqsResultData) + '&scrollTop=' + res.scrollTop
					})
				}).exec();
			},
			getScreenHeight() {
				uni.getSystemInfo({
					success: (res) => {
						this.containerHeight = res.windowHeight + 'px';
						console.log("当前屏幕高度:"+this.containerHeight)
						this.containerWidth = res.windowWidth + 'px';
					}
				});
			}
		}
	}
</script>

<style lang="scss" scoped scoped>
	page,
	view {
		/* display: flex; */
	}

	page {
		min-height: 100%;
		background-color: #FFFFFF;
	}

	.riskAssessment {
		width: 100%;
		background-color: #FFFFFF;
		position: fixed;
		top: env(safe-area-inset-top); /* 兼容 iOS 11.0-11.4 */
		top: constant(safe-area-inset-top); /* 兼容 iOS 12.0-12.1 */
		left: 0;
		touch-action: none;
		// left: 0;
		// top: 0;
		// right: 0;
		// bottom: 0;
		// padding-top: env(safe-area-inset-top); /* 兼容 iOS 11.0-11.4 */
		// padding-top: constant(safe-area-inset-top); /* 兼容 iOS 12.0-12.1 */
	}

	.status_bar {
		height: 5%;
		width: 100%;
		background-color: #ffffff;
	}

	.top_view {
		height: 5%;
		width: 100%;
		position: fixed;
		background-color: #F8F8F8;
		top: 0;
		z-index: 999;
		align-items: center;
	}

	.type {
		display: inline-flex;
		align-items: center;
		padding: 0 5px;
		margin-left: 15px;
		border-radius: 2px;
		font-size: 12px;
	}

	.part-title {
		display: flex;
		margin: 0px 7px;
		background-color: #ffffff;
		padding: 2px;
		position: relative;
		border-radius: 3px;
	}

	.title-t {
		flex: 1;
		align-items: center;
		position: relative;
		width: 100%;
		padding: 10px;
	}

	.infor-list {
		display: block;
		width: 100%;
		margin-top: 5px;
		height: 80%;
		box-sizing: border-box;

		.list {
			display: flex;
			padding: 5upx 0upx;

			// border-bottom:1upx solid #ececec;
			.img {
				width: 64upx;
				height: 64upx;
				padding-right: 28upx;
				float: left;
			}

			.list-part {
				flex: 1;
				display: block;
				justify-content: space-between;
				align-items: center;

				text:last-of-type {
					position: relative;
					display: flex;
					justify-content: flex-start;
					align-items: center;
					color: #000000;
				}
			}
		}
	}

	.links {
		width: 100%;
		overflow-x: scroll;
		padding: 4px 0px;
		height: 4%;
		display: flex;
		align-items: center;
		top: 48;

		.each-links {
			display: flex;
			flex: 1;
			justify-content: center;
			align-items: center;
			white-space: nowrap;
			height: 52upx;
			padding: 0 10px;

			text {
				text-align: center;
				color: #727272;
				font-size: 28upx;
				position: relative;
			}

			.colorActive::after {
				content: '';
				position: absolute;
				width: 40upx;
				height: 6upx;
				background-color: #0289fe;
				bottom: -12upx;
				left: 50%;
				margin-left: -20upx;
				border-radius: 4upx;

			}

			.colorActive {
				color: #000000;
				font-size: 30upx;
			}
		}
	}

	.testcase {
		border-radius: 5px;
		background-color: #e6f7ff;
		color: #0780d8;
		height: 30px;
		font-size: 12px;
		width: 116px;
		margin: inherit;
		// text-align: center;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.testResult {
		border-radius: 5px;
		background-color: #e6f7ff;
		color: #0780d8;
		height: 30px;
		font-size: 12px;
		width: 80px;
		margin-left: 15px;
		// margin: inherit;
		// text-align: center;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.edit {
		color: #2b9c94;
		font-size: 14px;
		font-weight: bold;
		padding: 6% 3%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.red-dot-text::before {
		content: "●";
		/* 使用圆点符号作为文字前的标记 */
		color: red;
		/* 设置圆点的颜色为红色 */
		display: inline-block;
		width: 1em;
		/* 调整圆点的位置 */
		font-size: 1.5em;
	}

	.checkbox {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-left: 5px;
	}
</style>
